/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-txp-slider {
	background-color: #eaeaea;
}

.txp-dynamic-align-center {
	text-align: center;
}
.txp-dynamic-align-left {
	text-align: left;
}
.txp-dynamic-align-right {
	text-align: right;
}

.swiper-wrapper {
	aspect-ratio: 16/9;	
}

.swiper-slide {

	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
	position: relative;

	.txp-slider-gradient-overlay {
		display: block;
		position: absolute;
		left: 0px;
		top: 0px;
		right: 0px;
		bottom: 0px;
		z-index: 101;
		opacity: .48;
	}	
	.txp-slider-content-wrap {
		display: block;
		position: relative;
		z-index: 102;
		margin: 0px auto;
		padding: 0px 48px;
		text-align: center;

		h2.txp-post-title {
			color: #FFF;
			margin-top: 12px;
			margin-bottom: 12px;
		}
		
		.txp-publish-date {
			color: #FFF;
			font-size: 1em;
		}
		a.txp-post-link {
			padding: 12px 24px;
			-webkit-border-radius: 4px;
			border-radius: 4px;
			display: inline-table;
			text-decoration: none;
			color: #FFF;
			background-color: rgba(64, 64, 64, 0.88);
			margin-top: 12px;

			&:hover {
				text-decoration: none;;
				background-color: rgba(32, 32, 32, 0.96);
			}
			&:visited {
				text-decoration: none;
			}			
			.txp-publish-date {
				color: #FFF;
			}
		}
			
			
	}
}

.txp-dynamic-align-left .txp-slider-content-wrap {
	text-align: left;
}
.txp-dynamic-align-center .txp-slider-content-wrap {
	text-align: center;
}
.txp-dynamic-align-right .txp-slider-content-wrap {
	text-align: right;
}

.txp-slider-content-wrap {
	top: 50%;
	transform: translateY(-50%);
}

h2.txp-post-title {
	line-height:  1em;
	font-size: 2.4em;
	font-size: 2vw;

	transition-property: all;
	transition-duration: 0.4s;
	transition-timing-function: ease-in-out;
	transition-delay: .2s;
}

.swiper-slide-active h2.txp-post-title {
	margin-top: 12px;
}